








import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";

@Component
export default class Gomoney extends Vue {
  @Prop() readonly value!: string;
  selectType(type: string) {
    if (type !== "+" && type !== "-") {
      throw new Error('type is unknown');
    }
    this.$emit('update:value', type);
  }
}
